const BASE_URL = 'https://pixabay.com/api/?image_type=photo&orientation=horizontal';
const apiKey = '19184731-b5d48d0d64c274e2586e4050a';


/*=============Функция выполняющая фетч к АПИ==============*/

function fetchImageByQuery(imageQuery) {
    const url = `${BASE_URL}&q=${imageQuery}&page=1&per_page=12&key=${apiKey}`;
    return fetch(url)
        .then(res => res.json())
        .then(({hits}) => hits);
        // .then(response => {
        //         console.log(response.json());
        //         return response.json();
        //     } 
        //     // console.log(response.status);
        // }).then(data => {
        //     console.log(data.hits);
        //     return data.hits;
        // });
}



export default {fetchImageByQuery}